
.cell {
  display: inline-block;
  text-align: center;
  width: 22px;
  height: 22px;
  margin: 0 1px 1px 1px;
}
.cell-none {
  background-color: #ccc;
}
.cell-start {
  background-color: #00ff00;
}
.cell-goal {
  background-color: #00d1b8;
}
.cell-obstacle {
  background-color: #555;
}
.cell-path {
  background-color: #ffb5b5;
}
.direction {
  width: 24px;
  height: 24px;
  font-size: 24px;
  color: #eee;
  overflow: hidden;
}



